.ufive_uptop {
  box-sizing: border-box;
  height: 50px;
  width: 50px;
  opacity: 0;
  cursor: pointer;
  z-index: 100;
  border: none;
  border-radius: 50%;
  outline: none;
  margin-right: -30px;
  transition: opacity .3s, margin-right .5s ease-out, background-color .3s;
  animation: pulse 1s linear infinite;
  display: block;
  position: fixed;
  bottom: 50px;
  right: 50px;
}

.ufive_uptop:hover, .ufive_uptop:focus {
  background-color: #f2d72b;
}

@keyframes pulse {
  0% {
    transform: scale(1);
  }

  50% {
    transform: scale(1.1);
  }

  100% {
    transform: scale(1);
  }
}

.ufive_upview {
  opacity: .6;
  margin-right: 0;
  transition: opacity .3s, margin-right .5s ease-out;
}

.ufive_uptop:before, .ufive_uptop:after {
  content: "";
  height: 18px;
  width: 2px;
  background-color: #000;
  position: absolute;
  top: 16px;
}

.ufive_uptop:before {
  left: 18px;
  transform: rotate(45deg);
}

.ufive_uptop:after {
  right: 18px;
  transform: rotate(-45deg);
}

